#petal-container {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
  pointer-events: none;
  z-index: 0;
}

.petal {
  position: absolute;
  top: -50px;
  width: 10px;
  height: 10px;
  background-image: url("/src/imgs/petal.png");
  background-size: cover;
  opacity: 0.7;
  animation: fall linear infinite;
}

@keyframes fall {
  0% {
    transform: translateX(0) rotate(0deg);
    top: -50px;
  }
  100% {
    transform: translateX(100px) rotate(360deg);
    top: 100vh;
  }
}
