@import "../fonts/fonts.css";

#choice-body {
  font-family: "Merienda";
  text-align: center;
  align-content: center;
  min-width: 100vw;
  width: 100%;
  color: #5c6b73;
  padding-top: 2rem;
  min-height: 100vh;
  height: 100%;
  background-image: linear-gradient(to bottom right, #e0fbfc, #5c6b73);
  z-index: 2;
}
#wait-twice {
  animation: fadeIn 10s;
  display: flex;
  max-width: 100vw;
  justify-content: center;
  gap: 3rem;
  padding: 1rem;
  margin-top: 2rem;
  z-index: 2;
}
#detailed,
#basic {
  display: inline-block;
  /* margin: 1rem; */
  text-align: left;
  background-color: #9db4c0;
  padding: 2rem;
  border-style: solid;
  border-width: 1px;
  border-color: #253237;
  border-radius: 10px;
  max-width: 500px;
  color: #253237;
  z-index: 2;
}
#get-started-basic,
#get-started-detailed {
  text-align: center;
  font-size: 1.5rem;
  position: sticky;
  top: 100%;
  z-index: 2;
}
#get-started-basic button,
#get-started-detailed button {
  background-color: #e0fbfc;
  border-radius: 50px;
  border-style: solid;
  box-sizing: border-box;
  color: #253237;
  cursor: pointer;
  flex-shrink: 0;
  font-size: 20px;
  font-weight: 500;
  height: 4rem;
  padding: 0 2rem;
  text-align: center;
  transition: all 0.5s;
  border-width: 0.5px;
  user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;
  z-index: 2;
}

#get-started-basic button:hover {
  box-shadow: #253237 0 1px 30px;
  transition-duration: 0.1s;
}
#get-started-detailed button:hover {
  box-shadow: #253237 0 1px 30px;
  transition-duration: 0.1s;
}
@media (min-width: 768px) {
  .button-36 {
    padding: 0 2.6rem;
  }
}
#detailed h1,
#basic h1 {
  text-align: center;
  z-index: 2;
}
#detailed button,
#basic button {
  text-align: center;
  z-index: 2;
}
#wait-typewriter {
  max-width: 1000px;
  margin: auto;
  width: auto;
  animation: fadeIn 7s;
  z-index: 2;
}
@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  10% {
    opacity: 0;
  }
  20% {
    opacity: 0;
  }
  30% {
    opacity: 0;
  }
  40% {
    opacity: 0;
  }
  50% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
