@import "../fonts/fonts.css";

.question {
  background-color: #5c6b73;
  margin-top: 1rem;
  margin-bottom: 1rem;
  padding: 2rem;
  border-radius: 25px;
  text-align: left;
  animation: fadeIn 2.5s linear forwards;
  font-weight: 700;
}
.options {
  font-weight: 100;
}
@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  10% {
    opacity: 0;
  }
  20% {
    opacity: 0;
  }
  30% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
#basic-body h1 {
  color: #253237;
}
#menu-bar-choices:focus,
#change-type:focus {
  outline: none;
  box-shadow: none;
}
#menu-bar-choices:hover,
#change-type:hover {
  transform: scale(1.2);
}
#basic-body {
  font-family: "Merienda";
  text-align: center;
  align-content: center;
  padding-top: 1rem;
  /* min-width: 100vw; */
  max-width: 100vw;
  color: #c2dfe3;
  min-height: 100vh;
  height: 100%;
  background-image: linear-gradient(to bottom right, #e0fbfc, #5c6b73);
}
#basic-body Form {
  text-align: right;
  width: 50vw;
  margin: auto;
  padding-bottom: 1rem;
}
#basic-body Form Button {
  border-radius: 10px;
  background-color: #5c6b73;
  border-color: #e0fbfc;
}
#progress-bar-basic {
  position: relative;
  top: -100;
}
